.header {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;

  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    @include padding-top(4rem);
    @include padding-bottom(4rem); }

  &__logo {
    display: block; } }

.logo {

  &__img-wrap {
    width: 107px; }

  &__img {
    display: block; } }

.nav {

  &__link--open {
    display: block;
    @include font-size(1rem);
    color: #9374F0;

    @include mw(768) {
      font-size: 0; } } }
